<template lang="html">
  <div class="call-notes-list">
    <div class="call-notes-list-item">
      <div class="call-notes-list-item__time clearfix">
        <span class="date fl">{{time}}</span>
        <span class="money fr" v-if="isReceive">扣费¥{{fee}}.00</span>
      </div>
      <div class="call-notes-list-item__detail clearfix">
        <span class="number fl"><span class="number-left fl" :style="{maxWidth: numberW}">{{text}}{{name}}</span><u class="fl" v-if="isReceive">({{number}})</u></span>
        <span class="minute fr">{{talkTime}}</span>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    isReceive: {
      type: Boolean,
      default: false
    },
    number: {
      type: String,
      default: ''
    },
    text: {
      type: String,
      default: ''
    },
    name: {
      type: String,
      default: ''
    },
    time: {
      type: String,
      default: ''
    },
    talkTime: {
      type: String,
      default: ''
    },
    numberW: {
      type: String,
      default: '2.5rem'
    },
    fee: {
      default: ''
    }
  }
}
</script>

<style lang="less" scoped>
@import '../../assets/css/variable.less';

.call-notes-list {
  .call-notes-list-item {
    height: 1.2rem;
    padding: .07rem .22rem 0;
    background: @whiteColor;
    .call-notes-list-item__time {
      font-size: .32rem;
      line-height: .5rem;
      .date {
        color: @midleColor;
      }
      .money {
        color: @lightColor;
      }
    }
    .call-notes-list-item__detail {
      font-size: .3rem;
      line-height: .62rem;
      .minute {
        color: @lightColor;
      }
      .number {
        display: inline-block;
        height: .62rem;
        overflow: hidden;
        .number-left {
          display: inline-block;
          height: .62rem;
          white-space: nowrap;
          text-overflow: ellipsis;
          overflow-x: hidden;
        }
      }
    }
  }
}
</style>



