<template lang="html">
  <div class="account-safe-page">
    <!-- 头部 -->
    <default-head header="我的拨打记录"></default-head>
    <div class="giveList"
      v-infinite-scroll="loadMore"
      :infinite-scroll-disabled="loading"
      :infinite-scroll-distance="10"
      :infinite-scroll-immediate-check="true"
    >
      <call-notes-list v-for="(item, index) in callNotes" :key="item.index" :class="{'borderB': item.index !== callNotes.length-1}" :isReceive=false numberW="4.6rem" :number="item.mobile" text="呼出电话" :name="item.userName" :time="item.createTime" :talkTime="item.talkTime | toFormatMinute" :fee="item.fee"></call-notes-list>
    </div>

    <no-data v-if="!this.callNotes.length"></no-data>
  </div>
</template>

<script>
import api from '@/api'
import utils from '@/utils/utils'
import _ from 'underscore'
import minutils from '@/utils/minUtils'
import noData from '@/components/common/noData'
import defaultHead from '@/components/head/defaultHead'
import callNotesList from '@/components/callNotes/callNotesList'

export default {
  data () {
    return {
      loading: false,
      pageSize: 11,
      pageIndex: 1,
      pages: 1,
      callNotes: []
    }
  },
  computed: {
    userId: function () {
      return utils.userId()
    }
  },
  components: { defaultHead, callNotesList, noData },
  mounted () {
    this.loadMore()
  },
  methods: {
    loadMore: _.throttle(function () {
      // 关闭无线滚动
      this.loading = true
      if (this.pageIndex === 1) {
        this.getGiveList(this.userId, this.pageIndex++, this.pageSize)
      } else if ((this.pageIndex - 1) * this.pageSize > this.pages) {
        return
      } else {
        this.getGiveList(this.userId, this.pageIndex++, this.pageSize)
      }
    }, 300),
    getGiveList (userId, pageIndex, pageSize) {
      minutils.IndicatorOpen('加载中')
      api.callNotesGive({userId: userId, pageIndex: pageIndex, pageSize: pageSize}).then((response) => {
        minutils.IndicatorClose()
        let result = response.body
        if (result.isSuccess) {
          this.pages = result.data.total
          this.callNotes = [...this.callNotes, ...result.data.callList]
        }
      })
    }
  }
}
</script>

<style lang="less" scoped>
@import '../assets/css/variable.less';

.account-safe-page {
  padding-top: .88rem;

}

</style>



